body {
    background: whitesmoke;
    transition: 0.5s;
}


.hero-section {
    display: flex;
    align-items: center;
    background-color: #fff;
    justify-content: space-around;
    flex-wrap: wrap;
    background-position: rotate(120deg);
    height: 90vh;
    transition: 0.5s;
   box-shadow: 0px 0px 0px grey;
}

.hero-content {
    z-index: 100;
}

.hero-img img {
    width: 100%;
    height: 70vh;
    z-index: 0;
    transition: 0.5s;
   text-shadow: 0px 0px 20px grey;
}


.hero-section h1 {
    font-size: 80px;
    font-weight: 300;
 
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.hero-section button {
    padding: 10px;
    border-radius: 5px;
    width: fit-content;
    background: rgb(149, 104, 237);
    display: flex;
    align-items: center;
    gap: 3px;
    border: none;
    outline: none;
margin-top: 20px;
    cursor: pointer;
    color: #ffff;
    transition: 0.5s;
}

/* hero service */
.hero-payment ,.hero-customer,.hero-delivery {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 30px;
    width: 300px;
 
}
.hero-service {
   
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 30px;
}
.hero-service div h2 {
    font-weight:600;
    font-size: 20px;
}
.hero-service div{
   line-height: 10px;
}
.hero-service-icon {
    color: #fff;
    padding: 12px;
    background: rgb(149, 104, 237);
    border-radius: 5px;
}

@media(max-width:600px) {
    .hero-img img {
        height: 45vh;
        width: 320px;
    }

    .hero-section h1 {
        font-size: 30px;
    }

    .hero-section h2 {
        font-size: 20px;
       
    }

    .hero-content {
        padding: 20px;
    }

    .hero-more-products img {
        height: 40px;
        width: 45px;
    }

   .hero-section {
    flex-direction: column-reverse;
   }
   .hero-service h3 {
    font-weight: 600;
    font-size: 19px;
   }
   
}

