.product-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;

    width: 100%;
    gap: 40px;
    margin-top: 20px;
    background-color: white;

}

.product {
    width: 300px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    border-radius: 5px;
    box-shadow: 0px 0px 1px grey;
    background-color: whitesmoke;
}

.product-not-found {
 
    height: 50vh;
    display: flex;
    justify-content: center;
    align-items: center;
    align-items: center;
}
.product-not-found h1 {
    text-align: center;
}


.product img {
    width: 70%;
    transition: 0.5s;
}

