.contact-section {
    padding: 30px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 50px;


}

form {
    width: 400px;
}

.contact-others {
    width: 400px;
    display: flex;
    flex-direction: column;

}

.contact-others div p {
    display: flex;
    align-items: center;
    gap: 10px;
}

.contact-section h1 {
    text-align: center;
}

form div {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-top: 34px;
    gap: 5px;
}

.contact-section form button {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 8px;
    padding: 12px;
    font-size: 17px;
    width: 130px;
    background: rgb(149, 104, 237);
    border: none;
    outline: none;
    color: #fff;
    border-radius: 5px;
    cursor: pointer;
}

.contact-iconss {
    color: orangered;
}

.send-msg {
    font-size: 16px;
}

.contact-section input {
    padding: 15px;
    /* border-radius: 5px; */
    width: 80%;
    border: none;
    outline: none;
    letter-spacing: 1px;
    box-shadow: 0px 0px 1px grey;
    background-color: whitesmoke;
}

.input-msg {
    height: 80px;
}

.contact-social-icon {
    /* color: rgb(149, 104, 237); */
    color: grey;

}

.contact-follow span {
    margin-right: 5px;
    cursor: pointer;
}

.contact-follow span:hover .contact-social-icon {
    transform: scale(1.2);
    color: orangered;
    transition: 0.5s;
}

@media(max-width:600px) {
    .contact-section h2 {
        font-weight: 600;
        font-size: 18px;
    }
}