.about-inf {
    padding: 60px;

    line-height: 24px;
    text-align: center;
    display: flex;
    flex-direction: column;
    gap: 40px;
    background-color:#fff;
}
.about-section {
    background-color: white;
}


.team-about h1 {
    text-align: center;
    line-height: 50px;

}
.team-about {
    background-color: whitesmoke;
    padding-top: 20px;
}
.team-img {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 30px;
    flex-wrap: wrap;
    margin-top: 30px;
  
}

.team-img img {
    width: 60%;
   
}
.team-img div {
    text-align: center;
    font-weight: 600;
}

@media (max-width:600px) {
    .about-inf {
        padding: 20px;
    
    }
    .team-about h1 {
        font-size: 22px;
    }
}