.navigate-btn {
   background-color:#fff;
    color: #000;
    border: none;
    outline: none;
    cursor: pointer;
    display: flex;
    align-items: center;
    gap: 5px;
    color: #000;
}
.hide {
    display: none;
}

.single-product-img img {
    width: 100%;
    height: 82vh;
}

.single-product {
    display: flex;
    justify-content: center;
    padding: 30px;
    gap: 40px;
    flex-wrap: wrap;
}

.single-product .rating-star {
    color: rgb(205, 178, 3);
}

.price-inf {
    color: rgb(152, 0, 41);
}

.single-product .price {
    font-weight: 600;
    font-size: 22px;
}

.price-section {
    line-height: 10px;
}

.service-inf {
    display: flex;
    gap: 30px;
}
.service-inf div {
    text-align: center;
}
.service-icon {
    color: rgb(111, 50, 0);
}
.recomanded {
    margin-top: 40px;
}

.single-brand {
    font-weight: 600;
    font-size: 20px;
    color: rgb(255, 142, 101);
}
.quantity {
    display: flex;
    gap: 15px;
    margin-top: 20px;
    margin-bottom: 20px;
}
.quantity button {
    background-color:#fff;
    border: none;
    outline: 1px solid #000;
    border-radius: 50%;
  
    cursor: pointer;
  font-size: 20px;
}
.add-cart-btn {
    width: 70%;
    padding: 10px;
    margin-top: 40px;
    background: none;
    border: none;
  font-weight: 400;
    background-color: rgb(217, 199, 4);
    color: #241807;
    cursor: pointer;
}

.available-stock {
    color: orangered;
   
}
@media (max-width:900px) {
    .hide {
        display: block;
    }
    .slug-single {
        display: none;
    }
    .single-product-img img {
        height: 60vh;
    }
    .recomanded h1 {
        font-size: 19px;
    }
    .single-product {
        flex-direction: column;
    }
}