footer {
    background-color: rgb(44, 41, 45);
  margin-top: 50px;
  
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
  gap: 30px;
    padding: 35px;
   
}

.footer-links ,.footer-subscribe, .footer-contact {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    color: #fff;
    gap: 10px;
}
.footer-subscribe input {
    padding: 10px;
    width: 100%;
    border: none;
    outline: 1px solid #fff;
    background-color: rgb(44, 41, 45);
    border-radius: 5px;
    color: #fff;
    letter-spacing: 1px;
}

.footer-subscribe button {
    padding: 10px;
    width: 70%;
    border-radius: 5px;
    border: none;
    outline: none;
    cursor: pointer;
    background-color:orangered;
    color: #fff;

}
footer .nav-link-footer {
    text-decoration: none;
    color: #ffff;
}
footer p {
    text-transform: uppercase;
    color: rgb(180, 75, 245);
}

.social-footer {
    display: flex;
    gap: 15px;
    margin-top: 10px;
}