.nav-bar {
    /* background: #1e1d1d; */
    background-color: rgb(44, 41, 45);
    margin: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: sticky;
    top: 0%;
    width: 100%;
    height: 60px;
    transition: 0.5s;
    z-index: 1000;
}

.nav-1 {
    display: flex;
    align-items: center;
    gap: 30px;
    z-index: 100;
   margin-left: 50px;
}
.nav-1 h2 {
    color:  rgb(181, 145, 253);
    margin-right: 20px;
    font-size: 20px;
}


.nav-2 {
    display: flex;
    align-items: center;
    gap: 30px;
    position: fixed;
    top: 18px;
    right: 40px;
}

.search-bar {
    display: flex;
    margin-left: 10px;
    align-items: center;
    height: 30px;
    width: 100%;
    border: none;
   z-index: -100;
    background-color: rgb(44, 41, 45);
    color: #fff;
    transition: 0.5s;
    border-radius: 5px;
  
}
.search-bar input {
    width: 100%;
    background-color: rgb(44, 41, 45);
    border: none;
    outline: none;
    color:  rgb(181, 145, 253);;
}

.nav-2 .cart-link p {
    color: rgb(255, 255, 255);
    background-color: rgb(181, 145, 253);
    ;
    height: 20px;
    width: 20px;
    font-size: 14px;
    position: absolute;
    bottom: 2px;
    left: 15px;
    text-align: center;
    border-radius: 50%;
}

.cart-link {
    position: relative;
}

.nav-bar .cart-link {
    display: flex;
    align-items: center;
}



.nav-bar li {
    list-style-type: none;

}

.nav-bar li .nav-link {
    color: #ffffff;
}

.toggle-icon {
    display: none;
}

.menu-icon {
    color: #e0e0e0;

}

.visible {
    visibility: visible;
}

.expand-navbar {

    height: 200px;
    padding-top: 20px;

}

@media (max-width:600px) {
    .toggle-icon {
        display: block;
    }

    .nav-bar {
        overflow: hidden;
    }

    .nav-1 {
        justify-content: center;
        flex-direction: column;
        gap: 15px;
    }

    .visible {
        visibility: hidden;
        transition: 0.1s;
    }
    .nav-1 h2 {
        display: none;
    }
}