.home {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  
}
.latest-product {
    text-align: center;
    font-size: 40px;
}

.service-hero div {
    width: 300px;
   display: flex;
 box-shadow: 0px 0px 5px grey;
 border-radius: 10px;
justify-content: center;
}
.latest--service {
display: flex;
flex-direction: column;
gap: 100px;
margin-top: 30px;
}

@media(max-width:600px) {
    .latest-product{
        font-size: 19px;
    }
    .latest-products {
        margin-top: 0px;
    }
   
}
@media(max-width:1020px) {
   .latest-products {
    margin-top: 250px;
   }
}

@media(max-width:600px) {
   .latest-products {
margin-top: 80px;
   }
   .home {
    gap: 60px;
   }
}

@media(min-width:600px) {
    .service-of-hero {
        margin-top: 100px;
        margin-bottom: 80px;
    }

}