.cart-product {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    gap: 20px;
    margin-top: 10px;
}

.cart-product img {
    width: 10%;
}

.cart-product .cart-quantity button {
    background: none;
    border: none;
    outline: none;
    cursor: pointer;
    font-size: 17px;
}

.cart-heading {
    display: flex;
    justify-content: space-around;
}

.cart-product-list h2 {
    font-weight: 600;
}

.cart-items {
    padding: 30px;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}

.order-summary {
    display: flex;
    align-items: center;

    flex-direction: column;
    width: 250px;

}

.sub-total {
    width: 100%;

}

.purchase {
    width: 100%;
    display: block;
    margin: auto;
    padding: 10px;
    margin-top: 10px;
    background: rgb(23, 205, 81);
    border: none;
    outline: none;
    border-radius: 5px;
    cursor: pointer;
    color: #fff;
}
.back-shop {
    width: 100%;
    display: block;
    margin: auto;
    padding: 9px;
    margin-top: 10px;
    background:none;
    border: none;
    border-radius: 5px;
    outline: 1px solid #000;
    transition: 0.5s;
    cursor: pointer;
    text-decoration: none;
}
.back-link {
    text-decoration: non10;
}
.back-shop:hover {
    background:#000000;
    color: #fff;
}
.final-total-text {
    font-weight: 600;
    font-size: 17px;
}
.discount-text {
    color: rgb(23, 117, 23);
}
.order-summary h2 {
    width: 100%;
    padding: 5px;
    text-align: center;
}

.sub-total span {
    display: flex;
    justify-content: space-between;
}

.delete-icon {
    color: orangered;
    cursor: pointer;
}


@media (max-width:650px) {
    .cart-product img {
        width: 13%;
    }

    .cart-product-list h2 {
        font-size: 17px;
    }

    .order-summary {
        margin-top: 30px;
    }
}